import React, { useEffect, useState } from 'react'
import { CustomButton, CustomSpinner } from '../../globalComponents'
import { useDispatch, useSelector } from 'react-redux'
import { pagination } from '../../../utils/Constants.config'
import ApproveRejectModal from '../../modal/ApproveRejectModal'
import { useCustomTranslation } from '../../../translation'
import CreateUpdateHoliday from './CreateUpdateHoliday'
import HolidayTable from './HolidayTable'
import { locationActions, commonActions, branchActions, holidaysActions } from '../../../core/resource/actions'
import UploadHoliday from './UploadHoliday'
const Holiday = () => {
    const { t } = useCustomTranslation()
    const dispatch = useDispatch()
    const {
        getAllCountriesListPending,
        getAllCountriesListRes,
        getAllCountriesListError,

        getAllStateFromCountryListPending,
        getAllStateFromCountryListRes,
        getAllStateFromCountryListError,

        getCityListFromStatePending,
        getCityListFromStateRes,
        getCityListFromStateError,

    } = useSelector(state => state.location)
    const {
        getAppConstantDataPending,
        getAppConstantDataRes,
        getAppConstantDataError,

        getDateConstantPending,
        getDateConstantRes,
        getDateConstantError,
    } = useSelector(state => state.common)
    const {
        getAllBranchesRes,
        getAllBranchesPending,
        getAllBranchesError
    } = useSelector(state => state.branch)
    const {
        getHolidayRes,
        getHolidayPending,
        getHolidayError,

        createHolidayPending,
        createHolidayRes,
        createHolidayError,

        updateHolidayPending,
        updateHolidayRes,
        updateHolidayError,

        deleteHolidayPending,
        deleteHolidayRes,
        deleteHolidayError,

        uploadHolidayPending,
        uploadHolidayRes,
        uploadHolidayError,
    } = useSelector(state => state.holiday)
    const [isOpenCreateUpdateHoliday, setIsOpenCreateUpdateHoliday] = useState(false)
    const [selectedHolidayData, setSelectedHolidayData] = useState('')
    const [currentPage, setCurrentPage] = useState(1)
    const [isShowDeleteHolidayModal, setIsShowDeleteHolidayModal] = useState(false)
    const [deleteHolidayId, setDeleteHolidayId] = useState('')
    const [allCountriesListData, setAllCountriesListData] = useState('')
    const [allStateListData, setAllStateListData] = useState('')
    const [allCityList, setAllCityList] = useState('')
    const [appConstantData, setAppConstantData] = useState('')
    const [dateConstantData, setDateConstantData] = useState('')
    const [allHolidayListData, setAllHolidayListData] = useState('')
    const [allBranches, setAllBranches] = useState([])
    const [openModel,setOpenModel] = useState(false)

    /* Create Update Holiday */
    const toggleCreateUpdateHolidayModal = (data) => {
        if (!data) {
            setSelectedHolidayData('')
        } else {
            setSelectedHolidayData(data)
        }
        // dispatch(locationActions.handleClearLocationStore('HANDLE_CLEAR_GET_CITY_LIST_FROM_STATE_STORE'))
        // dispatch(locationActions.handleClearLocationStore('HANDLE_CLEAR_GET_ALL_STATE_FROM_COUNTRY_LIST_STORE'))
        dispatch(commonActions.handleClearGetDateConstantStore('HANDLE_CLEAR_GET_DATE_CONSTANT_STORE'))
        setIsOpenCreateUpdateHoliday(!isOpenCreateUpdateHoliday)
    }

    /* Delete Holiday */
    const toggleHolidayDeleteModal = (id) => {
        if (id) {
            setDeleteHolidayId(id)
        }
        setIsShowDeleteHolidayModal(!isShowDeleteHolidayModal)
    }

    const handleDeleteHoliday = (type) => {
        if (type) {
            const params = {
                _id: deleteHolidayId
            }
            dispatch(holidaysActions?.deleteHolidayRequest(params))
        } else {
            toggleHolidayDeleteModal()
        }
    };

    useEffect(() => {
        if (!deleteHolidayPending) {
            if (deleteHolidayError) {
                dispatch(holidaysActions.handleClearHolidayStore("HANDLE_CLEAR_DELETE_HOLIDAY_STORE"))
                return;
            }
            if (deleteHolidayRes) {
                if (deleteHolidayRes?.code === 200) {
                    getAllHoliday()
                    dispatch(holidaysActions.handleClearHolidayStore("HANDLE_CLEAR_DELETE_HOLIDAY_STORE"))
                    toggleHolidayDeleteModal()
                }
            }
        }
    }, [
        deleteHolidayRes,
        deleteHolidayPending,
        deleteHolidayError
    ]);

    const handleCreateUpdateHoliday = (params) => {
        if (selectedHolidayData) {
            params['_id'] = selectedHolidayData?._id || selectedHolidayData?.id
            dispatch(holidaysActions.updateHolidayRequest(params));
        }
        else {
            dispatch(holidaysActions.createHolidayRequest(params));
        }
    }
        useEffect(() => {
        if (!createHolidayPending) {
            if (createHolidayError) {
                dispatch(holidaysActions.handleClearHolidayStore('HANDLE_CLEAR_CREATE_HOLIDAY_STORE'))
                return;
            }
            if (createHolidayRes) {
                if (createHolidayRes?.code === 200) {
                    dispatch(holidaysActions.handleClearHolidayStore('HANDLE_CLEAR_CREATE_HOLIDAY_STORE'))
                    getAllHoliday()
                    setIsOpenCreateUpdateHoliday(!isOpenCreateUpdateHoliday)
                }
            }
        }
    }, [
        createHolidayRes,
        createHolidayError,
        createHolidayPending
    ]);


    useEffect(() => {
        if (!updateHolidayPending) {
            if (updateHolidayError) {
                dispatch(holidaysActions.handleClearHolidayStore("HANDLE_CLEAR_UPDATE_HOLIDAY_STORE"))
                return;
            }
            if (updateHolidayRes) {
                if (updateHolidayRes?.code === 200) {
                    dispatch(holidaysActions.handleClearHolidayStore("HANDLE_CLEAR_UPDATE_HOLIDAY_STORE"))
                    getAllHoliday()
                    setIsOpenCreateUpdateHoliday(!isOpenCreateUpdateHoliday)
                }
            }
        }
    }, [
        updateHolidayRes,
        updateHolidayError,
        updateHolidayPending
    ]);


    const setFilterValues = (data) => {
        const tempData = []
        data && data?.map((item) => {
            tempData.push(item?.id || item)
        })
        return tempData
    }

    useEffect(() => {
        getAllCountryList()
        getAppConstantData()
        getDateConstantData()
        getAllHoliday()
    }, [])
    

    /* Get All Branches */
    const getAllBranches = (id) => {
        const cityData = []
        id && id?.map((item) => {
            cityData.push(item?._id || item)
        })
        const params = {
            city_id: cityData || ''
        }
        dispatch(branchActions.getAllBranchesRequest(params));
    }

    useEffect(() => {
        if (!getAllBranchesPending) {
            if (getAllBranchesError) {
                return;
            }
            if (getAllBranchesRes) {
                if (getAllBranchesRes?.code === 200) {
                    setAllBranches(getAllBranchesRes?.data)
                }
            }
        }
    }, [
        getAllBranchesRes,
        getAllBranchesError,
        getAllBranchesPending
    ]);

    /*Get App Constant Data */
    const getAppConstantData = () => {
        dispatch(commonActions.getAppConstantDataRequest())
    }
    useEffect(() => {
        if (!getAppConstantDataPending) {
            if (getAppConstantDataError) {
                return;
            }
            if (getAppConstantDataRes) {
                if (getAppConstantDataRes?.code === 200) {
                    setAppConstantData(getAppConstantDataRes?.data)
                }
            }
        }
    }, [
        getAppConstantDataRes,
        getAppConstantDataError,
        getAppConstantDataPending
    ]);

    /*Get Date Constant Data */
    const getDateConstantData = () => {
        dispatch(commonActions.getDateConstantRequest())
    }
    useEffect(() => {
        if (!getDateConstantPending) {
            if (getDateConstantError) {
                return;
            }
            if (getDateConstantRes) {
                if (getDateConstantRes?.code === 200) {
                    setDateConstantData(getDateConstantRes?.data)
                }
            }
        }
    }, [
        getDateConstantPending,
        getDateConstantRes,
        getDateConstantError
    ]);

    const getAllHoliday = () => {
        dispatch(holidaysActions.getHolidayRequest());
    }

    useEffect(() => {
        if (!getHolidayPending) {
            if (getHolidayError) {
                return;
            }
            if (getHolidayRes) {
                if (getHolidayRes?.code === 200) {
                    const getAllHolidayResData = getHolidayRes?.data
                    setAllHolidayListData(getAllHolidayResData)
                }
            }
        }
    }, [
        getHolidayRes,
        getHolidayError,
        getHolidayPending
    ]);

    const getAllCountryList = () => {
        dispatch(locationActions.getAllCountriesListRequest());
    }

    useEffect(() => {
        if (!getAllCountriesListPending) {
            if (getAllCountriesListError) {
                return;
            }
            if (getAllCountriesListRes) {
                if (getAllCountriesListRes?.code === 200) {
                    const getAllCountriesListResData = getAllCountriesListRes?.data
                    setAllCountriesListData(getAllCountriesListResData)
                }
            }
        }
    }, [
        getAllCountriesListRes,
        getAllCountriesListError,
        getAllCountriesListPending
    ]);


    /* Get All State From Country list */
    const getAllStateFromCountryList = (id) => {
        const country_id = setFilterValues(id)
        const params = {
            country_id: country_id || ''
        }
        dispatch(locationActions.getAllStateFromCountryListRequest(params));
    }

    useEffect(() => {
        if (!getAllStateFromCountryListPending) {
            if (getAllStateFromCountryListError) {
                return;
            }
            if (getAllStateFromCountryListRes) {
                if (getAllStateFromCountryListRes?.code === 200) {
                    setAllStateListData(getAllStateFromCountryListRes?.data)
                }
            }
        }
    }, [
        getAllStateFromCountryListRes,
        getAllStateFromCountryListError,
        getAllStateFromCountryListPending
    ]);

    const handleStateCity = (value, type) => {
        if (type === "country") {
            getAllStateFromCountryList(value)
            setAllStateListData([])
            setAllCityList([])
        }
        if (type === "state") {
            getCityListFromState(value)
            setAllCityList([])
        }
        else if (type === "city") {
            getAllBranches(value)
            setAllBranches([])
        }
    }

    /* get all  location list */
    const getCityListFromState = (id) => {
        const state_id = setFilterValues(id)
        const params = {
            state_id: state_id || ''
        }
        dispatch(locationActions.getCityListFromStateRequest(params));
    }

    useEffect(() => {
        if (!getCityListFromStatePending) {
            if (getCityListFromStateError) {
                return;
            }
            if (getCityListFromStateRes) {
                if (getCityListFromStateRes?.code === 200) {
                    setAllCityList(getCityListFromStateRes?.data)
                }
            }
        }
    }, [
        getCityListFromStateRes,
        getCityListFromStateError,
        getCityListFromStatePending
    ]);

    useEffect(() => {
        if (!uploadHolidayPending) {
            if (uploadHolidayError) {
                return;
            }
            if (uploadHolidayRes) { 
                if (uploadHolidayRes?.code === 200) {
                    dispatch(holidaysActions.handleClearHolidayStore("HANDLE_CLEAR_UPLOAD_HOLIDAY_STORE"))
                    setOpenModel(!openModel)
                    getAllHoliday()
                }
            }
        }
    }, [
        uploadHolidayRes,
        uploadHolidayError,
        uploadHolidayPending
    ]);


    return (
        <>
            <>
                <div className="d-flex  justify-content-end ">
                <CustomButton
                        classNames={"btn btn-primary gradientBg me-2"}
                        handleClick={() => setOpenModel(!openModel)}
                        text={t("btns.uploadHoliday")}
                    />
                    <CustomButton
                        classNames={"btn btn-primary gradientBg"}
                        handleClick={() => toggleCreateUpdateHolidayModal()}
                        text={t("btns.addHoliday")}
                    />
                </div>
                <CreateUpdateHoliday
                    openModel={isOpenCreateUpdateHoliday}
                    handleOpenClose={toggleCreateUpdateHolidayModal}
                    handleCreateUpdateHoliday={handleCreateUpdateHoliday}
                    selectedHolidayData={selectedHolidayData}
                    allCountriesListData={allCountriesListData}
                    handleStateCity={handleStateCity}
                    allStateListData={allStateListData}
                    allCityList={allCityList}
                    appConstantData={appConstantData}
                    dateConstantData={dateConstantData}
                    allBranches={allBranches}
                    getAllStateFromCountryList={getAllStateFromCountryList}
                    getCityListFromState={getCityListFromState}
                    getAllBranches={getAllBranches}
                    getAllHoliday={getAllHoliday}

                />
                <HolidayTable
                    setSelectedHolidayData={setSelectedHolidayData}
                    toggleCreateUpdateHolidayModal={toggleCreateUpdateHolidayModal}
                    currentPage={currentPage}
                    setCurrentPage={setCurrentPage}
                    allHolidayListData={allHolidayListData}
                    isLoading={getHolidayPending}
                    toggleHolidayDeleteModal={toggleHolidayDeleteModal}
                    viewType="admin"
                />

                {isShowDeleteHolidayModal && (
                    <ApproveRejectModal
                        onApproveReject={handleDeleteHoliday}
                        isShow={isShowDeleteHolidayModal}
                        desc={t("alerts.deletePopup.desc").replace('$1', "Holiday")}
                        btnRightLoading={deleteHolidayPending}
                        btnTextRight={t("btns.delete")}
                        btnTextLeft={t("btns.cancel")}

                    />)
                }
                {openModel && <UploadHoliday
                    openModel={openModel}
                    setOpenModel={setOpenModel}
                />}
                {uploadHolidayPending && <CustomSpinner isShow={uploadHolidayPending} size="lg" />}
            </>
        </>
    )
}

export default Holiday
