import React, { useState,useEffect } from 'react'
import { CustomButton, CustomError } from '../../globalComponents'
import { CustCheckbox, Input, Label } from '../../commanComponent'
import { useCustomTranslation } from '../../../translation'
import { useSelector, useDispatch } from 'react-redux'
import { holidaysActions } from '../../../core/resource/actions'
import { convertToTS } from '../../../utils/AppHelper'

const UploadHoliday = (props) => {
    const { openModel, setOpenModel } = props
    const {
        sampleDownloadPending,
        sampleDownloadRes,
        sampleDownloadError,
    } = useSelector(state => state.holiday)
    const dispatch = useDispatch()
    const { t } = useCustomTranslation()
    const [errors, setErrors] = useState({})
    const [fileName, setFileName] = useState('')

    useEffect(() => {
        if (!sampleDownloadPending) {
            if (sampleDownloadError) {
                return;
            }
            if (sampleDownloadRes) {
                if (sampleDownloadRes?.code === 200) {
                    window.open(sampleDownloadRes?.data, '_blank')
                    dispatch(holidaysActions.handleClearHolidayStore("HANDLE_CLEAR_SAMPLE_STORE"))
                }
            }
        }
    }, [
        sampleDownloadRes,
        sampleDownloadError,
        sampleDownloadPending
    ]);

    const downloadSample = ()=>{
        dispatch(holidaysActions.sampleDownloadRequest());
    }
    const handleFileChange =(e)=>{
        const file = e.target.files[0]
        setFileName(file)
    
    }
    const onSubmitFile = ()=>{
        const formData = new FormData();
        formData.append('file', fileName)
        
        dispatch(holidaysActions.uploadHolidayRequest(formData));
    }
    return (
        <div>
            <div className="modal fade attendanceViewodal" style={{ display: openModel ? "unset !important" : "none !important", opacity: openModel ? "unset" : "0" }} >
                <div className="modal-dialog modal-dialog-centered">
                    <div className="modal-content">
                        <div className="modal-header border-0 pb-0">
                            <h1 className="modal-title fs-5 fw-bold text-primary">Upload Holiday</h1>
                            <button type="button" className="btn-close" onClick={() => { setOpenModel(!openModel) }}></button>
                        </div>
                        <div className="modal-body">
                                <div className='row'>
                                    <div className="form-group col-md-12 text-end py-2">
                                        <button
                                            className="btn btn-secondary float-right ms-5 "
                                            title=""
                                            style={{ backgroundColor: "", color: "black" }}
                                            onClick={()=>downloadSample()}
                                        >
                                            Download Sample
                                        </button>
                                    </div>
                                </div>
                                {/* // } */}
                               
                                    <div className="form-group col-md">
                                    <Label text={t("labels.uploadHoliday")} required={true} />
                                        <input

                                            type="file"
                                            placeholder=""
                                            className="is-untouched is-pristine av-invalid form-control mt-2"
                                            name="file"
                                            id="Name"
                                            onChange={(e)=>handleFileChange(e)}
                                            required
                                        />
                                    </div>

                            <div className='text-center'>
                            <button
                                type="button"
                                className="btn btn-primary gradientBg mt-2 "
                                onClick={()=>onSubmitFile()}
                            >
                                Upload Holiday
                            </button></div>
                        </div>
                    </div>
                </div>
            </div>
            {openModel && <div className="modal-backdrop fade show"></div>}
        </div>
    )
}

export default UploadHoliday
