import React, { useEffect, useState } from 'react'
import { CustomButton, CustomError } from '../../globalComponents'
import { createShortName, defaultMultiSelectOptions } from '../../../utils/common'
import CustomSelect from '../../commanComponent/CustomSelect'
import { CustCheckbox, Input, Label } from '../../commanComponent'
import { validateSchema } from '../../../utils/ValidationHelper'
import { createUpdateHolidaySchema } from '../../../utils/ValidationSchema'
import { useSelector, useDispatch } from 'react-redux'
import { useCustomTranslation } from '../../../translation'
import { locationActions } from '../../../core/resource/actions'
import moment from 'moment';


const CreateUpdateHoliday = (props) => {
    const dispatch = useDispatch()
    const { createHolidayPending } = useSelector(state => state?.holiday)
    const { t } = useCustomTranslation()
    const {
        selectedHolidayData,
        openModel,
        handleOpenClose,
        handleCreateUpdateHoliday,
        allCountriesListData,
        handleStateCity,
        allStateListData,
        allCityList,
        appConstantData,
        dateConstantData,
        allBranches,
        getAllHoliday,
        getAllStateFromCountryList,
        getAllBranches,
        getCityListFromState,
    } = props
    const [errors, setErrors] = useState({})
    const [datesArray, setDatesArray] = useState([])
    const [addCriteria, setAddCriteria] = useState(false)
    const [createUpdateHolidayFormData, setCreateUpdateHolidayFormData] = useState({
        name: '',
        from_date: '',
        to_date: '',
        country: [],
        state: [],
        city: [],
        branch: [],
        gender: [],
        description: '',
        restricted: false,
        notify_before_days: 0,
        is_notify_to_employee: false,
        is_reprocess_leave: false,
    });


    const handleFromChange = (e, fieldName) => {
        const name = fieldName || e.target?.name;
        const { value, type, checked } = e?.target ? e?.target : { value: e };
        setCreateUpdateHolidayFormData(prevState => {
            let updatedState = {
                ...prevState,
                [name]: type === 'checkbox' ? checked : value,
            };

            // Automatically create short name when 'name' is updated
            if (name === 'name') {
                updatedState = {
                    ...updatedState,
                    short_name: createShortName(value),
                };
            }
            if (name === 'country') {
                updatedState = {
                    ...updatedState,
                    state: [],  // Reset state when country changes
                    city: [],   // Reset city as well
                };
                handleStateCity(value, name);
            }
            if (name === 'state') {
                updatedState = {
                    ...updatedState,
                    city: [],  // Reset city when state changes
                };
                handleStateCity(value, name);
            } if (name === 'city') {
                updatedState = {
                    ...updatedState,
                    branch: [],  // Reset branch when city changes
                };
                handleStateCity(value, name);
            }  
            if (name === "from_date") {
                updatedState = {
                    ...updatedState,
                    to_date: '',
                };
                setDatesArray([])
            }
            if (name === "to_date") {
                updatedState = {
                    ...updatedState,
                }
                let data = getDatesArray(createUpdateHolidayFormData?.from_date, value)
                setDatesArray(data)
            } if (name === "restricted") {
                updatedState = {
                    ...updatedState,
                }
                let data = getDatesArray(createUpdateHolidayFormData?.from_date, createUpdateHolidayFormData?.to_date)
                setDatesArray(data)
            }

            return updatedState;
        });
        setErrors(prevErrors => ({
            ...prevErrors,
            [name]: undefined,
        }));
    };

    const setFilterValues = (data) => {
        const tempData = []
        data && data?.map((item) => {
            tempData.push(item?.value || item)
        })
        return tempData
    }

    const submitCreateUpdateHoliday = async () => {
        const validationResponse = await validateSchema(createUpdateHolidaySchema, createUpdateHolidayFormData);
        if (validationResponse.isValid) {
            const dateArrayFormate = filterDateArray(datesArray)
            const countryIds = setFilterValues(createUpdateHolidayFormData?.country);
            const stateIds = setFilterValues(createUpdateHolidayFormData?.state);
            const cityIds = setFilterValues(createUpdateHolidayFormData?.city);
            const branchIds = setFilterValues(createUpdateHolidayFormData?.branch);
            const gender = setFilterValues(createUpdateHolidayFormData?.gender);
            const params = {
                name: createUpdateHolidayFormData?.name,
                from_date: moment(createUpdateHolidayFormData?.from_date).format("YYYY-MM-DD"),
                to_date: moment(createUpdateHolidayFormData?.to_date).format("YYYY-MM-DD"),
                country: countryIds,
                state: stateIds,
                city: cityIds,
                branch: branchIds,
                gender: createUpdateHolidayFormData?.gender ? gender : setFilterValues(appConstantData?.gender),
                description: createUpdateHolidayFormData?.description || '',
                restricted: createUpdateHolidayFormData?.restricted,
                notify_before_days: createUpdateHolidayFormData?.notify_before_days?.value,
                is_notify_to_employee: createUpdateHolidayFormData?.is_notify_to_employee,
                is_reprocess_leave: createUpdateHolidayFormData?.is_reprocess_leave,
                holiday_data: dateArrayFormate,
            }
            setErrors({})
            handleCreateUpdateHoliday(params)
            resetForm();
            getAllHoliday();
        } else {
            setErrors(validationResponse.errors);
        }
    }

    useEffect(() => {
        return (() => {
            resetForm()
        })
    }, [openModel])
    useEffect(()=>{

    },[allStateListData])
    
    useEffect(() => {
        if (selectedHolidayData) {
            getAllStateFromCountryList(selectedHolidayData?.state);
            getCityListFromState(selectedHolidayData?.city);
            getAllBranches(selectedHolidayData?.branch)
            const country = defaultMultiSelectOptions(allCountriesListData,selectedHolidayData?.country)
            const state = defaultMultiSelectOptions(allStateListData,selectedHolidayData?.state)
            const city = defaultMultiSelectOptions(allCityList,selectedHolidayData?.city)
            const branch = defaultMultiSelectOptions(allBranches,selectedHolidayData?.branch)
            const gender = defaultMultiSelectOptions(appConstantData?.gender,selectedHolidayData?.gender)
            setDatesArray(selectedHolidayData?.holiday_data)
            setCreateUpdateHolidayFormData({
                name: selectedHolidayData?.name,
                from_date: selectedHolidayData?.from_date,
                to_date: selectedHolidayData?.to_date,
                country: country,
                state: state,
                city: city,
                branch: branch,
                gender: gender,
                description: selectedHolidayData?.description,
                restricted: selectedHolidayData?.restricted,
                notify_before_days: {value:selectedHolidayData?.notify_before_days,label:selectedHolidayData?.notify_before_days},
                is_notify_to_employee: selectedHolidayData?.is_notify_to_employee,
                is_reprocess_leave: selectedHolidayData?.is_reprocess_leave,
            })
        }
    }, [selectedHolidayData])

    const resetForm = () => {
        setCreateUpdateHolidayFormData({
            name: '',
            from_date: '',
            to_date: '',
            country: [],
            state: [],
            city: [],
            branch: [],
            gender: '',
            description: '',
            restricted: false,
            notify_before_days: 0,
            is_notify_to_employee: false,
            is_reprocess_leave: false,
        })
        setErrors({})
        setDatesArray([])
    };

    const getDatesArray = (startDate, endDate) => {
        const dates = [];
        let currentDate = moment(startDate).format("YYYY-MM-DD");
        while (currentDate <= moment(endDate).format("YYYY-MM-DD")) {
            dates.push({ "date": moment(currentDate).format("ddd DD-MMM-YYYY"), "apply_for": appConstantData?.dayType[0] });
            currentDate = moment(currentDate).add(1, 'day').format("YYYY-MM-DD");
        }
        return dates;
    };

    const handleOnDayChange = (e, index) => {
        setDatesArray(prevState => {
            const updatedLogs = [...prevState];
            updatedLogs[index] = {
                ...updatedLogs[index],
                apply_for: e,
            };
            return updatedLogs;
        });
    }

    const tableForDatesArray = () => {
        return (
            <div className='divAddHoliday mb-3'>
                <table className='tableAddDateHoliday'>
                    {datesArray && datesArray.map((data, index) => (
                        <tr className='mb-3'>
                            <td className='tdHoliday'>{moment(data?.date).format("ddd DD-MMM-YYYY")}</td>
                            <td>
                                <CustomSelect
                                    data={appConstantData?.dayType}
                                    isDisabled={createUpdateHolidayFormData?.restricted ? true : false}
                                    placeholder={t("placeholders.selectDayType")}
                                    value={selectedHolidayData?.holiday_data ? {label:data?.apply_for,value:data?.apply_for} : data?.apply_for}
                                    handleChange={(e) => handleOnDayChange(e, index)}
                                />
                            </td>
                        </tr>))}
                </table>
            </div>
        )
    }
    const filterDateArray = (data) => {
        const tempDateArray = []
        data?.map((item, index) => {
            tempDateArray.push({ ["date"]: moment(item?.date).format("YYYY-MM-DD"), apply_for: item?.apply_for?.value })
        })
        return tempDateArray
    };

    const renderCriteria = () => {
        return (<>
            <div className="col-md-3">
                <select className="form-control">
                    <option>Select</option>
                    <option>Shift 1</option>
                    <option>Shift 2</option>
                </select>
            </div>
            <div className='col-md-1'>
                <span className="input-group-text">is</span>
            </div>
            <div className='col-md-3'>
                <input type="text" className="form-control" placeholder="Select" />
            </div>
            <div className="col-md-2">
                <button className="btn btn-outline-danger" onClick={() => setAddCriteria(false)}>
                    <i className="fa fa-trash"></i>
                </button>
            </div>
        </>)
    }


    return (
        <>
            <div className={`offcanvas offcanvas-end  ${openModel ? 'show' : ''}`}
                tabIndex={-1}
                style={{
                    visibility: openModel ? 'visible' : 'hidden',
                    width: "800px"
                }}
                id="interviewDetail"
            >
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-5 fw-bold text-primary">
                        {selectedHolidayData ? t("headings.updateHoliday") : t("headings.addHoliday")}
                    </h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => handleOpenClose()}
                    />
                </div>


                <div className="offcanvas-body myRequest ">
                    <div className="formPanel">
                        <div className="row">

                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.holidayName")} required={true} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.name")}
                                        value={createUpdateHolidayFormData?.name}
                                        name="name"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                    {errors?.name && (
                                        <CustomError text={errors.name} />
                                    )}
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.fromDate")} required={true} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <Input
                                        type="date"
                                        placeholder={t("placeholders.fromDate")}
                                        value={createUpdateHolidayFormData?.from_date}
                                        name="from_date"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                    {errors?.from_date && (
                                        <CustomError text={errors.from_date} />
                                    )}
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.toDate")} required={true} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <Input
                                        type="date"
                                        value={createUpdateHolidayFormData?.to_date}
                                        name="to_date"
                                        onChange={(e) => handleFromChange(e)}
                                        min={createUpdateHolidayFormData?.from_date}
                                    />
                                    {errors?.to_date && (
                                        <CustomError text={errors.to_date} />
                                    )}
                                    {datesArray?.length > 0 &&
                                        <div style={{ display: "inline-flex", marginTop: "5px" }}>
                                            <CustCheckbox
                                                style={{ fontSize: "medium" }}
                                                key="restricted"
                                                id="restricted"
                                                label={"Restricted"}
                                                checked={createUpdateHolidayFormData?.restricted}
                                                handleChange={(e) => handleFromChange(e)}
                                                name="restricted"
                                            />
                                        </div>}
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                            </div>
                            <div className="col-md-9">
                                {datesArray?.length > 0 && tableForDatesArray()}
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.country")} required={true} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <CustomSelect
                                        isMulti={true}
                                        data={allCountriesListData}
                                        placeholder={t("placeholders.selectCountry")}
                                        value={createUpdateHolidayFormData?.country || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "country")}
                                        name="country"
                                    />
                                    {errors?.country && (
                                        <CustomError text={errors.country} />
                                    )}
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.state")} required={true} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <CustomSelect
                                        isMulti={true}
                                        data={allStateListData}
                                        placeholder={t("placeholders.selectState")}
                                        value={createUpdateHolidayFormData?.state || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "state")}
                                        name="state"
                                    />
                                    {errors?.state && (
                                        <CustomError text={errors.state} />
                                    )}
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.city")} required={true} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <CustomSelect
                                        isMulti={true}
                                        data={allCityList}
                                        placeholder={t("placeholders.selectCity")}
                                        value={createUpdateHolidayFormData?.city || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "city")}
                                        name="city"
                                    />
                                    {errors?.city && (
                                        <CustomError text={errors.city} />
                                    )}
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.branchName")} required={true} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <CustomSelect
                                        isMulti={true}
                                        data={allBranches}
                                        placeholder={t("placeholders.selectBranchName")}
                                        value={createUpdateHolidayFormData?.branch || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "branch")}
                                        name="branch"
                                    />
                                    {errors?.branch && (
                                        <CustomError text={errors.branch} />
                                    )}
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.gender")} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <CustomSelect
                                        isMulti={true}
                                        data={appConstantData?.gender}
                                        placeholder={t("placeholders.selectGender")}
                                        value={createUpdateHolidayFormData?.gender || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "gender")}
                                        name="gender"
                                    />
                                </div>
                            </div>
                        </div>
                        <div className="row  form-group mb-3">
                            <div className="col-md-3">
                                <Label text={t("labels.applicableFor")} />
                            </div>
                            {!addCriteria ?
                                <div className="col-md-3">
                                    <CustomButton
                                        text={t("btns.addCriteria")}
                                        classNames={"btn btn-primary"}
                                        handleClick={() => setAddCriteria(true)}
                                    /></div> : (renderCriteria())}
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.description")} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.description")}
                                        value={createUpdateHolidayFormData?.description}
                                        name="description"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.noOfDaysBefore")} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <CustomSelect
                                        data={dateConstantData}
                                        placeholder={t("placeholders.selectNoOfDays")}
                                        value={createUpdateHolidayFormData?.notify_before_days || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "notify_before_days")}
                                        name="notify_before_days"
                                    />
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-12">
                                <div className="form-group mb-3">
                                    <CustCheckbox
                                        key="is_notify_to_employee"
                                        id="is_notify_to_employee"
                                        label={"Notify applicable employees via feeds"}
                                        checked={createUpdateHolidayFormData?.is_notify_to_employee}
                                        handleChange={(e) => handleFromChange(e)}
                                        name="is_notify_to_employee"
                                    />
                                    <small>(They will receive a feed notification instantly once this holiday is saved)</small>
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-12">
                                <div className="form-group mb-3">
                                    <CustCheckbox
                                        key="is_reprocess_leave"
                                        id="is_reprocess_leave"
                                        label={"Reprocess leave applications based on this added holiday"}
                                        checked={createUpdateHolidayFormData?.is_reprocess_leave}
                                        handleChange={(e) => handleFromChange(e)}
                                        name="is_reprocess_leave"
                                    />
                                    <small>(Leaves that are already applied for this holiday will be reprocessed and the balance will be adjusted accordingly)</small>
                                </div>
                            </div>
                        </div>
                        <div className='row'>
                            <div className="col-md-12">
                                <div class="alert alert-warning" role="alert">
                                    <b>Note : </b>Shift based holiday will override the location based holiday.
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="offcanvas-footer">
                    <div className="d-flex align-items-center justify-content-end gap-3">
                        <CustomButton
                            text={selectedHolidayData ? t("btns.update") : t("btns.submit")}
                            classNames={"btn btn-org-gradient"}
                            handleClick={() => submitCreateUpdateHoliday()}
                            isLoading={createHolidayPending}
                        />
                    </div>
                </div>
            </div>
            {openModel && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default CreateUpdateHoliday
