import React, { useEffect, useMemo, useState } from 'react'
import { CustomTable } from '../../reuseableComponents'
import { getBadgeColor } from '../../../utils/common'
import { CustomButton } from '../../globalComponents'
import moment from 'moment'
import { useCustomTranslation } from '../../../translation'
import { all } from 'axios'

const HolidayTable = (props) => {
    const {t}=useCustomTranslation()
    const {
        setSelectedHolidayData,
        toggleCreateUpdateHolidayModal,
        currentPage,
        setCurrentPage,
        allHolidayListData,
        isLoading,
        toggleHolidayDeleteModal,
        viewType
    } = props

    const handleUpdateHoliday = (data) => {
        toggleCreateUpdateHolidayModal(data)
    }


    const scopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span>{index+1}</span>
        ),
        is_active: item => (
            <span
                className={`badge ${getBadgeColor(item.is_active ? "Active" : "Inactive")}`}
            >
                {item.is_active ? "Active" : "Inactive"}
            </span>
        ),
        days: item =>  moment(item.to_date).diff(moment(item.from_date), 'days')+1,
        created_at: item => moment(item.created_at).format("DD-MM-YYYY"),
        from_date: item => moment(item.from_date).format("DD-MM-YYYY"),
        to_date: item => moment(item.to_date).format("DD-MM-YYYY"),
        action: item => (
            <div className="actionGroup d-flex gap-2">
                {viewType ==="admin"&&<>
                <CustomButton
                    text={t("btns.update")}
                    classNames="btn btn-primary"
                    handleClick={() => {
                        handleUpdateHoliday(item);
                    }}
                />
                <CustomButton
                    text={t("btns.delete")}
                    handleClick={() => toggleHolidayDeleteModal(item._id)}
                    classNames="btn btn-danger"
                /></>}
            </div>
        )
    }), [setSelectedHolidayData,toggleCreateUpdateHolidayModal]);


    const fields = [
        { key: 'sn', label: 'S.N' },
        { key: 'name', label: 'Holiday Name' },
        { key: 'from_date', label: 'Holiday From' },
        { key: 'to_date', label: 'Holiday To' },
        { key: 'days', label: 'Days' },
        { key: 'action', label: 'Action' }
    ];
  return (
    <>
      <CustomTable
                data={allHolidayListData}
                fields={fields}
                isLoading={isLoading}
                pagination={true}
                currentPage={currentPage}
                setCurrentPage={setCurrentPage}
                tableName={"Holiday"}
                // onSearch={handleSearch}
                scopedSlots={scopedSlots}
            />
    </>
  )
}

export default HolidayTable
